<template>
  <div>
    <h1>欢迎来到快乐星球</h1>
    <div>token: {{ token }}</div>
    <div>name: {{ name }}</div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'index',
  data () {
    return {
      name: ''
    }
  },
  computed: {
    ...mapState({
      token: state => state.token
    })
  },
  mounted() {
    this.name = this.$store.getters.name
  }
}
</script>

